.input {
  display: inline-block;
  transition: transform 0.2s;
}
.input label {
  padding-left: 0.5rem;
  font-size: 1.2rem;
}
.input input {
  position: relative;
  top: -0.15rem;
  transform: scale(1.2);
}
.input:hover {
  transform: scale(1.1);
}
.header input {
  transform: scale(1.7);
}
.header label {
  padding-left: 1em;
}
.header h2 {
  display: inline-block;
}
.strikeThrough input:checked + label {
  text-decoration: line-through;
  color: rgba(136, 136, 136, 0.98);
}
.textInput {
  display: flex;
  flex-direction: column;
}
.textInput input {
  line-height: 1.3rem;
  max-width: 30rem;
  background-color: inherit;
  color: inherit;
  border: currentColor solid;
  padding: 0.5em 0.75em;
  border-radius: 0.25em;
}
.textInput label {
  margin-bottom: 0.5rem;
}
